import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import '../form4/Form4.css';

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import Timer from '../../timer'

// import required modules

import { Zon } from "../../containers";

import { Autoplay, Pagination, Navigation } from "swiper";
export default function Form4({ opacity, ...props }) {
    return (

        <div id="Form4">

            <div class="form1c">

                <div class="zoncont">

                    <Swiper
                        loop={true}
                        pagination={{
                            clickable: false,
                        }}
                        // autoplay={{
                        //     delay: 5000,
                        //     disableOnInteraction: false,
                        // }}
                        navigation={true}
                        modules={[Autoplay, Navigation, Pagination]}
                        className="mySwiper"
                        style={{
                            backgroundColor: '#d7d7d7', boxShadow: '0 10px 20px rgb(0 0 0 / 19%), 0 6px 6px rgb(0 0 0 / 23%)',
                            borderRadius: '10px', width: '100%', borderLeft: 'solid 10px #05b2ab', marginLeft: '-10px'
                        }}
                    >

                        <SwiperSlide>
                            <Timer />
                        </SwiperSlide>

                        <SwiperSlide >
                            <div class="h1-custom">Pågående laddning</div>
                            <Zon/>
                        </SwiperSlide>

                    </Swiper>

                </div> {props.children}</div>
        </div>

    );
}


