import React from 'react'

export default async function request({zon, pc}) {
         const response = await fetch('http://localhost:5001/getOutletInfo',
           {   
               method: "POST",
               headers:{
                 'Accept': 'application/json',
                 'Content-Type': 'application/json'
               },
               body: JSON.stringify({
                 "outletId": zon,
                 "providerCode": pc
               })
           })
             
             const xam = await response.json();
             console.log(xam);
             return xam; 
          }
