import { Component } from 'react'


import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import CancelIcon from '@mui/icons-material/Cancel';
import './Zone.css';
import Test from '../../Test';
import request from '../../request';
import { defaultStore } from '../../actions';
import { connect } from 'react-redux';

 class Zone extends Component {
  constructor(props){
    super(props);
    this.state = JSON.parse(window.localStorage.getItem('state')) || {
      outletId: '',
      provider: '',
      plugged: false,
      stationName: '\xA0',
      status: '',
      color:'',
      borderL: '',
    }
    
    
    
  }

  setState(state){
    window.localStorage.setItem('state', JSON.stringify(state));
    super.setState(state);
  }

 async componentDidMount(){
    //zon pc parameter från länk egentligen. Vid ändring ska ny variabel skickas.
    console.log(this.state.outletId);
    let jsonstring = await request({zon: this.props.change.outl, pc: 'EWAYS'});
    let getMessage = jsonstring.message;
    
    console.log(getMessage)
    let message = JSON.parse(getMessage);

    console.log(message.chargerDetails.publicOutletId)
    console.log(message.chargerDetails.provider.name)
    
    //översätta jsonstring
    this.setState({outletId: message.chargerDetails.publicOutletId,
                  provider: (message.chargerDetails.provider.name).toUpperCase(),
                  stationName: (message.chargerDetails.stationName).toLowerCase().replace(/[^a-zA-Z|å|ä|ö|Å|Ä|Ö]/g, ''),
                  status: (message.chargerDetails.status)});

    console.log(this.state.status);
  }

  contentSwitch(){
    switch (this.props.stepUpdate) {
      case 0:
        return(
          
            /* this.props.stepUpdate === 0||2||3 ? 
              
                  this.props.stepUpdate === 3 ? 
                  ''
                : 
                  ''
                  this.props.step
            :  */
              this.state.status === 'UNAVAILABLE' ? 
              <><div class="status" style={{color: '#b17717'}}><CancelIcon/><p>Ur funktion</p></div> <div class="providerContainer"><div class={'provider'+ this.state.provider}></div></div></>
                
              : 
                <><div class="status"><CheckCircleOutlineIcon/><p>Tillgänglig</p></div> <div class="providerContainer"><div class={'provider'+ this.state.provider}></div></div></>
                
                
              
        );
      case 2:
      return (
        <div class="h1-custom">Bekräftelse</div>
      );
    default:
        return '';
    }
  }

  styleSwitch(){
    switch (this.props.stepUpdate) {
      case 0:
        return(
          {
            borderLeft: this.state.status === 'UNAVAILABLE' 
          ? 
            'solid 10px #b17717'
          :
            'solid 10px #05b2ab'
          }
           
        );
      case 2:
      return (
        {
          borderLeft: 'none' 
        }
      );
      case 3:
        return(
          {
            borderLeft: 'none' 
          }
        );
    default:
        return '';
    }
  }

  
  render() {
  
    return (
      
      

      <div class="zon" style={this.styleSwitch()}>
      
      <div id="Head" class="head">
        <div class="headrow"> 
        {
          this.contentSwitch()
        }
        
          
        </div>
        
        <p class="ss" style={{marginTop: this.props.stepUpdate === 3 ? '20px' : '0px'}}>{this.state.stationName}</p> 
      </div>
      
      <div class="spec">
        <div class="zonid">
          <span>zon id</span>
          <span class="a">101</span>
        </div>
        <div class="uttagid">
          <span>uttag</span>
          <span class="a">{this.state.outletId}</span>
        </div>
        <div class="pris">
          <span>PRIS/kWh</span>
          <span class="a">3kr</span>
        </div>
        
      </div>
      {this.props.stepUpdate === 2 ? <div class="providerContainer"><div class={'provider'+ this.state.provider}></div></div> : ''}
      {this.props.stepUpdate === 3 ? <div class="providerContainer"><div class={'provider'+ this.state.provider}></div></div> : ''}
      {this.props.children}
      
      
      
      
      </div>
      
        
        
        )
  }
}

const mapStateToProps = (state) => {
  return {change: state.change,
          stepUpdate: state.stepUpdate}
}



export default connect(mapStateToProps)(Zone)
