import { combineReducers } from "redux";
import qrActivator from "./qrActivator";
import changeHandle from "./changeHandle";
import showStep from "./showStep";
import next from "./nextStep";
const allReducers = combineReducers({
    isQrActive: qrActivator,
    change: changeHandle,
    displayStep: showStep,
    stepUpdate: next


});
export default allReducers;