import * as React from "react";
import Button from "@mui/material/Button";
import Modal from "@mui/material/Modal";
import './popupwindow.css'
import { StopCharging } from "..";


export default class BasicModal extends React.Component {


    constructor(props) {
        super(props);

        this.state = {
            open: false
        }
        this.handleOpen = this.handleOpen.bind(this);
        this.handleClose = this.handleClose.bind(this);

    }
    handleOpen = () => {
        this.setState({ open: true })
    }

    handleClose = () => {
        this.setState({ open: false })
    }

    render() {
        return <div>
            <StopCharging onClick={this.handleOpen}> <text style={{ color: '#fff' }}>avlsuta laddning </text></StopCharging>
            <Modal
                open={this.state.open}
                onClose={(_, reason) => {
                    if (reason !== "backdropClick") {
                        this.handleClose();
                    }
                }}
            >
                <div className="body">
                    <div className="avlsluta-text">
                        Är du säker på att du vill avsluta laddningen?
                    </div>

                    <Button
                        className="yes-btn" onClick={this.props.wrapperFunction}
                    >
                        <text style={{ color: '#fff' }}>Ja</text>
                    </Button>

                    <Button
                        className="no-btn" onClick={this.handleClose}
                    >
                        <text style={{ color: '#fff' }}>Nej</text>
                    </Button>
                </div>
            </Modal>
        </div >

    }

}