'use-strict'
import React, { Component } from 'react'
import QrReader from 'modern-react-qr-reader'
import {connect} from 'react-redux'
import { activateQR } from "./actions";
import { handleChange } from './actions';

class Test extends Component {
 
  constructor(props) {
        super(props);
        
        //Hämta från länk!

        this.handleError = this.handleError.bind(this);
        this.handleScan = this.handleScan.bind(this);
    }

  
    

  /*   getOutletId(){
        return outletId;
    }
    getRef(){
      return ref;
  } */
  handleScan(data){
    if (data) {
        console.log(data);
        const trailingData = data.split(/[?]/).pop();

        let arr = trailingData.split(/.\w+=/).filter(Boolean); 
    
      
        let  outletId = arr[3];
        let  providerCode = arr[1];
          
         this.props.handleChange(outletId);
         activateQR();
          
          


        console.log(outletId);

    }
  }
 

/* getOutletInfo(arr){
  
    fetch('http://localhost:9094/getOutletInfo',
      {
          method: "POST",
          headers:{
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            "outletId": outletId,
            "providerCode": providerCode
          })
      })
      .then(function(res) {
        return res.json();
      })
      .then(function(myJson) {
        console.log(myJson);
        console.log(('{"name":"John", "age":30, "city":"New York"}'));
        const uö = JSON.parse('{"name":"John", "age":30, "city":"New York"}');
        console.log(uö.name);
        const u = JSON.parse(JSON.stringify(myJson));
        const v = JSON.parse(JSON.stringify(u.message))
        console.log(v.chargerDetails)
      })
      .catch((error) => {
        console.error('EROW:', error);
        console.log("fixman")   
      });
  } */
  //Based on link

  
  
  handleError = err => {
    console.error(err)

  }

  
  
  render() {
    return (
      <div>
        <QrReader
          delay={300}
          facingMode={"environment"}
          onError={this.handleError}
          onScan={this.handleScan}
          style={{ width: '100%' }}
        />
      </div>
    )
  }
}
const mapDispatchToProps = {
  
  activateQR, handleChange
  
}


export default connect(null, mapDispatchToProps)(Test)