import "./App.css";
import React from "react";
import { Form1, Form2, Form3, Form4 } from "./components";
import { Header } from "./containers";
import { Section } from "./containers";
import { Forms } from "./containers";
import CustomizedSteppers from "./components/stepper/demo";
import { ButtonContinue } from "./components";
import { QRscanButton } from "./components";
import { Zon } from "./containers";
import "./components/buttons/buttoncontinue.css";
import Form5 from "./components/form5/Form5";
import CircularProgress from '@mui/material/CircularProgress';
import Test from "./Test";
import { Demo } from './containers'


import {useSelector, useDispatch} from 'react-redux';
import { activateQR, nextStep } from "./actions";


const App = () => {

  const dispatch = useDispatch();
  
  const isQrActive = useSelector(state => state.isQrActive);
  const stepUpdate = useSelector(state => state.stepUpdate);

  const [opacity, opacitySet] = React.useState(1);
  const [opacity2, opacitySet2] = React.useState(1);
  const [opacity3, opacitySet3] = React.useState(1);





  const [chargeState, setChargeState] = React.useState(false);
  const [showStep, setDisplayStep] = React.useState(true);
  const [text, setText] = React.useState(<p>börja ladda</p>);
  const [boxText, setBoxText] = React.useState(<p></p>);

  const show = (
    <Zon>
      <QRscanButton onClick={() => QR()}/>

    </Zon>
  );

  const qrdiv = <div class="qrread">
    <Test /></div>;

  const one = (
    <>
      <div class="btn-text">Håll kameran mot QR-Koden</div>
      <ButtonContinue
        onClick={() => QR()}
        style={{ backgroundColor: "#d54040" }}
      >
        <p>avbryt</p>
      </ButtonContinue>
    </>
  );

  const two = (
    <>
      <div class="btn-text">
      </div>
      <div class="btn-cont">
        <ButtonContinue
          onClick={() => {
            fram();
          }}
        >
          <p>välj ett betalsätt</p>
        </ButtonContinue>
      </div>
    </>
  );

  function stopDisplay() {
    document.getElementsByClassName("d").disabled = true;
    // setDisplayStep(!showStep);
  }

  function QR() {  //metoden opacityset o setQrstate körs efter 400ms - osvs
    opacitySet(0);
    setTimeout(() => {
      opacitySet(1);
     dispatch(activateQR());
    }, 400);
  }

  // A function that disables börja ladda knappen när den söker
  function disableButton() {
    document.getElementById("stopCharge").disabled = true;
  }

  function beginCharge() {
    setBoxText(<p>Vi väntar på att laddningen ska börja. Se till att laddningskabeln är inkopplad</p>)
    setText(<p>försöker ansluta</p>);

    setChargeState(!chargeState);
    disableButton();
    setTimeout(() => {

      fram3();
    }, 2000);
  }

  function fram() {
    opacitySet(0);

    setTimeout(() => {
      opacitySet(1);
      dispatch(nextStep());
    }, 400);
  }

  function fram2() {
    opacitySet2(0);

    setTimeout(() => {
      dispatch(nextStep());
    }, 400);
  }

  function fram3() {
    opacitySet3(0);
    setDisplayStep(false);
    setTimeout(() => {
      dispatch(nextStep());
    }, 400);

  }

  var wrapperFunction = () => {
    fram();
    stopDisplay();
  }

  function getStepContent(step) {
    
    //PRELIMINÄR - stor del av koden kmr att inkapslas -----------------------------------------------------------
    switch (step) {
      case 0:
        return (
          <Form1 activeStep={stepUpdate} opacity={opacity}>
            {isQrActive ? qrdiv : show}
            
            <div class="btnholder">
              <div class="btn-box">{isQrActive ? one : two}</div>
            </div>
          </Form1>
        );
      case 1:
        return (
          <Form2 activeStep={stepUpdate} opacity2={opacity2}>
            <div class="btnholder">
              <div class="btn-box">
                <ButtonContinue
                  onClick={() => {
                    fram2();
                  }}
                >
                  <div class="payex"></div>
                  <div class="textbox">
                    <div class="text">
                      Kortregistrering
                      <div class="alttxt">via Swedbank Pay</div>
                    </div>
                  </div>

                </ButtonContinue>

                <div class="btn-text">
                  <p class="eller">eller</p>
                </div>

                <ButtonContinue>
                  <div class="swish"></div>
                  <div class="textbox">
                    <div class="text">
                      Swish
                      <div class="alttxt">Betala med Swish</div>
                    </div>
                  </div>

                </ButtonContinue>
              </div>

            </div>

          </Form2>
        );

      case 2:
        return (
          <Form3 activeStep={stepUpdate} opacity3={opacity3}>
            <Zon></Zon>
            <div class="btnholder">
            <div class="btn-box">
              <div class="btn-text"> {boxText} </div>
              <ButtonContinue id="stopCharge" style={{ color: 'white' }}
                onClick={() => {
                  beginCharge();
                }}
              ><div class="btnf3">
                  {text}
                </div>
                <CircularProgress color="inherit" style={{ display: chargeState ? 'block' : 'none', width: '25px', height: '25px', position: 'absolute', marginLeft: '65%' }} />
              </ButtonContinue>
            </div>
            </div>
          </Form3>
        );
      case 3:
        return (
          <Form4>
            <div class="btn-box">
              <div class="btn-text">
                Tryck för att avsluta
              </div>
              <Demo wrapperFunction={wrapperFunction} />
            </div>
          </Form4>
        );
      case 4:
        return (
          <Form5>

          </Form5>
        );
      default:
        throw new Error("Mis-step!");
    }
  }

  return (
    //STEPPER under section //Button css separat, button navigation transform
    <>
      <Header />
      <Section class="section">
        <CustomizedSteppers activeStep={stepUpdate} opacity={!isQrActive ? opacity3 : 0} showStep={showStep} />
        <Forms>{getStepContent(stepUpdate)}</Forms>
      </Section>
    </>
  );
};

export default App;
//Ändra klassnamn
//<Button onClick={() => {fram();}}>w World</Button>
