export const activateQR = () => {
    return{
        type: 'QRACTIVE'
    }
}

export const handleChange = (outle) => {
    return{
        type: 'UPDATE_STORE',
        outlet: outle
    }
}

export const nextStep = () => {
    return{
        type: 'NEXT_STEP'
    }
}

export const stepShow = () => {
    return{
        type: 'STEP_STATE'
    }
}
